.flights {
  flex: 1 0 auto;
  display: flex;
  justify-content: space-between;
  gap: 30px;
  padding: 50px;
}
@media (max-width: 1000px) {
  .flights {
    padding: 30px;
  }
}
@media (max-width: 767px) {
  .flights {
    flex-direction: column;
  }
  .flight__flight {
    text-align: right;
  }
}